"use client"

import { useState, useRef, useEffect } from "react"
import { User, LogOut, History, Settings, X, Heart, Newspaper, Film } from 'lucide-react'
import Link from "next/link"
import { useAuth } from "@/context/auth-context"
import { BookingHistory } from "@/components/booking-history"

export function ProfileDropdown() {
  const [isOpen, setIsOpen] = useState(false)
  const [showBookingHistory, setShowBookingHistory] = useState(false)
  const dropdownRef = useRef<HTMLDivElement>(null)
  const { user, logout } = useAuth()

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false)
      }
    }

    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  if (!user) return null

  const userInitial = user.name.charAt(0).toUpperCase()

  if (showBookingHistory) {
    return (
      <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4">
        <div className="bg-background rounded-lg p-6 max-w-2xl w-full max-h-[80vh] overflow-y-auto">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-2xl font-bold">Booking History</h1>
            <button
              onClick={() => setShowBookingHistory(false)}
              className="text-muted-foreground hover:text-foreground transition"
            >
              <X className="w-6 h-6" />
            </button>
          </div>
          <BookingHistory isOpen={true} />
        </div>
      </div>
    )
  }

  return (
    <div className="relative" ref={dropdownRef}>
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="w-10 h-10 rounded-full bg-accent flex items-center justify-center text-primary font-bold hover:bg-accent/90 transition"
      >
        {userInitial}
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-56 bg-white rounded-lg shadow-xl border border-gray-200 py-2 z-50">
          {/* User Info */}
          <div className="px-4 py-3 border-b border-gray-200">
            <p className="font-semibold text-gray-900">{user.name}</p>
            <p className="text-sm text-gray-600">{user.phone}</p>
            {user.email && <p className="text-sm text-gray-600">{user.email}</p>}
          </div>

          {/* Menu Items */}
          <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition">
            <User className="w-4 h-4" />
            <span className="text-sm font-medium">My Profile</span>
          </button>

          <Link href="/favorites" className="w-full">
            <button 
              onClick={() => setIsOpen(false)}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition"
            >
              <Heart className="w-4 h-4" />
              <span className="text-sm font-medium">My Favorites</span>
            </button>
          </Link>

          <button 
            onClick={() => {
              setShowBookingHistory(true)
              setIsOpen(false)
            }}
            className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition"
          >
            <History className="w-4 h-4" />
            <span className="text-sm font-medium">Booking History</span>
          </button>

          <Link href="/cinenews" className="w-full">
            <button 
              onClick={() => setIsOpen(false)}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition"
            >
              <Newspaper className="w-4 h-4" />
              <span className="text-sm font-medium">Cinema News</span>
            </button>
          </Link>

          <Link href="/trailers/upcoming" className="w-full">
            <button 
              onClick={() => setIsOpen(false)}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition"
            >
              <Film className="w-4 h-4" />
              <span className="text-sm font-medium">Trailers</span>
            </button>
          </Link>

          <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 transition">
            <Settings className="w-4 h-4" />
            <span className="text-sm font-medium">Settings</span>
          </button>

          <div className="border-t border-gray-200 mt-2 pt-2">
            <button
              onClick={() => {
                logout()
                setIsOpen(false)
              }}
              className="w-full flex items-center gap-3 px-4 py-3 text-red-600 hover:bg-red-50 transition"
            >
              <LogOut className="w-4 h-4" />
              <span className="text-sm font-medium">Logout</span>
            </button>
          </div>
        </div>
      )}
    </div>
  )
}
